.how-to-buy{margin-top:40px;padding:30px;display:flex;flex-direction:column;align-items:center;width:70%}.how-to-buy__description{font-size:20px;margin-bottom:50px;max-width:40%;color:#fff}.how-to-buy__description_bold{font-size:24px}.table{border-radius:10px;padding:2.5%;width:100%;display:grid;grid-template-rows:1.2fr 2fr;grid-template-columns:1fr 1fr;gap:15px}.table__title{text-align:center;color:#fff}.table__cell{display:flex;flex-direction:column;align-items:center}.table__cell:last-of-type{grid-column:1/3;background:none}.table__cell-content{margin-top:10px;display:flex;flex-direction:column;align-items:center;border:solid 3px #494949;border-radius:10px;width:100%;height:100%}.table__about-me{margin:auto 0 0;font-size:17px;color:#fff}.how-to-buy__instruction-list{list-style-type:none;margin:auto 0;padding:0}.how-to-buy__instruction{text-align:left;color:#fff}.how-to-buy__instruction::marker{content:"✱";color:#fff}.how-to-buy__art-marker{display:none;color:#fff}.how-to-buy__link{text-decoration:none}.how-to-buy__link:hover{opacity:.5}.how-to-buy__link_telegram{color:#50a5ff}.how-to-buy__link_whatsapp{color:#8ce7a9}.contacts__map{height:300px;width:100%;padding-top:2.5%;box-sizing:content-box}.contacts__location{margin-top:10px;display:flex;width:100%;justify-content:space-evenly}.contacts__location p{color:#fff}.leaflet-container{width:100%;height:100%;border-radius:10px;z-index:2}@media screen and (max-width: 1010px){.how-to-buy{padding:30px 0;width:80%}.table__title{padding:20px;font-size:22px;background-image:linear-gradient(-30deg, hsl(12, 82%, 54%), hsl(252, 82%, 57%));color:#000;border-radius:20px;border:1.5px solid #000}.table__about-me{margin:auto 0 0;font-size:1.15rem}.how-to-buy__instruction{text-align:center;font-size:1.15rem}.how-to-buy__art-marker{display:block}.how-to-buy__instruction::marker{content:""}.contacts__location p{font-size:16px}}@media screen and (max-width: 760px){.table{grid-template-rows:1.3fr 1fr 2.5fr;grid-template-columns:1fr;gap:50px}.table__cell:last-of-type{grid-column:auto;background:none}.table__cell-content{margin-top:20px;width:90%;max-width:380px}.how-to-buy__description{max-width:none}.contacts__map{padding-top:20px}.contacts__location{flex-direction:column}}@media screen and (max-width: 500px){.how-to-buy{padding:30px 0;width:100%}.how-to-buy__description{max-width:80%}.table{grid-template-rows:1.6fr 1.2fr 2.3fr;grid-template-columns:1fr;gap:35px}.contacts__map{height:250px;width:90%;padding-top:20px}.contacts__location p{font-size:14px}}